.MuiToolbar-regular {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.header__optionLineOne {
  font-size: 13px;
}

.header__optionBasket {
  color: #222222;
}

.header__nav {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
}

.header__option {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: #222222;
}

.header__nav > a {
  text-decoration: none;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header__optionLineOne:hover {
  color: lightgray;
  transition: all 0.2s ease-in-out;
}

.MuiSvgIcon-root {
  color: #222222;
}

.MuiSvgIcon-root:hover {
  color: lightgray;

}

.home {
display:-webkit-flex;
display:flex;
-webkit-justify-content: center;
        justify-content: center;
margin-left: auto;
margin-right: auto;
max-width: 1500px;

}


.home__product{
display: grid;
grid-template-columns: 1fr ;
}

.home__productscontainer{

    max-width: 1560px;
    min-width: 260px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
}

.home__sectionheading > h3{
font-size: 25px;
margin-bottom: 3px;
margin-top: 10px;
}

.home__sectionheading > p {

font-size: 13px;

}

