display: flex;

margin-left: 5px;
margin-right: 5px;

} */


.home__image{
width: 100%;

;
}


@media only screen and (min-width: 500px){

    .home__product{
        display: grid;
        grid-template-columns: 1fr 1fr;
        }

}


@media only screen and (min-width: 900px){

    .home__product{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        }

}
.product {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;

  -webkit-align-items: center;

          align-items: center;

  margin: 10px;
  padding: 20px;

  max-height: 400px;
  min-width: 100px;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product__info > button {
  background: #d8d8da;
  border: none;
  color: black;
  border-color: none;
  outline: none;
  font-size: 12px;
  border-radius: 5px;
  padding: 5px;
  cursor: pointer;
}

.product__price {
  margin-top: 5px;
  margin-bottom: 10px;
}

.product__info {
  margin-bottom: 3px;
  text-align: left;
    max-width: 200px;
}

.checkout{
    display:-webkit-flex;
    display:flex;
    padding:20px;
    background-color: white;
    height: -webkit-max-content;
    height: max-content;
    }
    
    
    .checkout__ad{
    width: 100%;
    margin-bottom: 10px;
    
    }
    
    
    .checkout__title{
    margin-right: 10px;
    padding:10px;
    border-bottom: 1px solid lightgray;
    
    
    }
.checkoutProduct{
